.breadcrumb {
}
.breadcrumb__items {
	display: flex;
}
.breadcrumb__item {
	font-size: 0.9375rem;
	font-weight: 700;
	line-height: 1.111em;
	color: #721C8299;

	&::after{
		content:'/'; 
		margin: 0 0.3125rem;
	}

	&:nth-child(1)	{
		&::before{
			content:''; 
			float: left; 
			width: 1rem; 
			height: 0.875rem; 
			margin-right: 0.375rem; 
			background-size: 100%; 
			background-position: center; 
			background-repeat: no-repeat; 
			background-image: url('data:image/svg+xml;base64,');
		}
	}
	&:last-child	{
		&::after{
			display: none;
		}
	}
}
.breadcrumb__item a {
	font-size: 0.9375rem;
	font-weight: 700;
	line-height: 1.111em;
	color: #721C82;
	transition: all 0.4s ease 0s;

	@media (any-hover: hover){
		 &:hover { 
			text-decoration: underline;
	  } 
	}
}
 